:root {
  --pri-font:"Roboto", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
  background-color: #1c1b1b;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

h2 {
  text-align: center;
  margin-top: 3.125rem;
  background: linear-gradient(93deg, #00dbde, #fc00ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

form {
  width: 21.875rem;
  margin: auto;
  position: relative;
  text-align: center;
}
form .input-container {
  position: relative;
  margin-bottom: 0.625rem;
}
form .input-container img {
  position: absolute;
  width: 1.25rem;
  left: 0.5rem;
  top: 0.75rem;
  align-items: center;
  transition: all 0.3s ease-in-out;
}
form .input-container i {
  position: absolute;
  left: 0.5rem;
  top: 0.6875rem;
  font-size: 1.375rem;
  transition: all 0.3s ease-in-out;
}
form .input-container input {
  padding: 0.875rem 2.8125rem;
  width: 100%;
  border: none;
  outline: none;
}
form .input-container input:focus {
  border: 1px solid #fc00ff;
}
form .output-container {
  width: 100%;
  padding: 0.875rem 0.9375rem;
}
form .output-container #output {
  color: white;
  text-align: left;
  font-weight: 600;
}
form button {
  border: none !important;
  width: 100%;
  margin-top: 0.625rem;
  padding: 0.875rem 2.8125rem;
  text-align: center;
  font-weight: 600;
  font-size: 0.9375rem;
  cursor: pointer;
  color: white;
  background: linear-gradient(93deg, #00dbde, #fc00ff);
  transition: all 0.3s ease-in-out;
}
form button:hover {
  opacity: 0.7;
}

footer {
  position: absolute;
  text-align: center;
  background: linear-gradient(93deg, #00dbde, #fc00ff);
  width: 100%;
  color: white;
  padding: 0.3125rem;
  bottom: 0;
}